@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@200;300;400&display=swap');

.nav {
  grid-area: n;
  font-family: 'Inconsolata', monospace;
  margin-top: 30px;

  .nav_list {
    padding: 0;
    margin: 0 auto;
    width: 30%;
    color: white;
    display: flex;
    flex-direction: column;
    list-style: none;

    .nav_item {
      margin-bottom: 30px; }

    .nav_item_link {
      text-decoration: none;

      img {
        width: 30px;
        height: 30px; }
      span {
        font-size: 25px; } }


    .nav_item:last-child {
      margin-bottom: 0; } } }

.nav_item_link span {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  color: white;
  text-transform: uppercase;
  font-size: 24px; }


.nav_item_link span:hover {
  color: hsl(211, 100%, 50%); }






.nav_item_link span:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(to right,hsl(211, 100%, 50%),hsl(179, 100%, 30%));
  visibility: hidden;

  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -o-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;

  -webkit-transform: scaleX(0);
  -moz-transform: scaleX(0);
  -o-transform: scaleX(0);
  transform: scaleX(0); }


.nav_item_link span:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  -moz-transform: scaleX(1);
  -o-transform: scaleX(1);
  transform: scaleX(1); }
